.map-wrapper {
  height: 100vh;
  max-height: 800px;
}

/* TOP LEFT WIDGET */
.esri-ui-top-left {
  position: absolute;
  width: 30%;
  height: 50%;
}
.esri-ui-top-left .esri-zoom {
  position: absolute;
  top: 77px;
  left: 20px;
  border-radius: 5px;
}
.esri-ui-top-left .esri-zoom .esri-widget--button {
  width: 60px;
  height: 60px;
  min-width: 100%;
  min-height: 100%;
}
.esri-ui-top-left .esri-zoom .esri-widget--button span {
  font-size: 30px;
}

.esri-ui-corner .esri-basemap-toggle {
  position: absolute;
  left: 20px;
}
.esri-ui-corner .esri-basemap-thumbnail {
  border-radius: 5px;
  outline: none;
  border: 0;
}
.esri-ui-corner .esri-basemap-toggle .esri-basemap-toggle__image--secondary {
  display: none;
  outline: none;
  border: 0;
}
.esri-attribution {
  display: none !important;
}
.esri-popup__main-container {
  width: auto !important;
  max-width: 460px;
}
.esri-popup__main-container .esri-popup__header {
  background: #002650 !important;
  height: 70px;
  align-items: center;
  display: flex;
}
.esri-popup__main-container .esri-popup__header-container {
  margin-left: 20px;
}
.esri-popup__main-container .esri-popup__header h2 {
  font-family: 'Open Sans';
  font-size: 20px;
  font-weight: 700;
  color: #fff !important;
  line-height: 25px;
}
.esri-popup__main-container .esri-popup__header h2:hover {
  background: inherit;
}
.esri-popup__main-container .esri-feature__content-element,
.esri-popup__main-container .esri-feature__content-element tbody {
  background: #68a0b9 !important;
}
.esri-popup__main-container .esri-popup__content {
  margin: 20px;
  overflow: scroll;
  overflow-x: hidden;
  scrollbar-width: none;
  -ms-overflow-style: none;

  scrollbar-width: thin;
  scrollbar-color: orange #002650;
}
.esri-popup__main-container .esri-popup__content::-webkit-scrollbar {
  width: 5px;
}
.esri-popup__main-container .esri-popup__content::-webkit-scrollbar-thumb {
  background-color: #002650;
  border-radius: 20px;
}
.esri-popup__main-container .esri-popup__content .esri-feature__main-container {
  background: #002650;
}
.esri-popup__main-container .esri-popup__content table td {
  font-family: 'Open Sans';
  font-size: 18px;
  font-weight: 300;
  color: #000;
  word-break: break-word;
  width: 50%;
  padding: 12px;
  border: none;
}
.esri-popup__main-container .esri-popup__content table tr td:first-child {
  font-weight: 400;
}
.esri-popup__main-container .esri-popup__content table tr {
  background: #fff;
  border: none;
  border-bottom: 2px solid #d7d7d7;
}
.esri-popup__main-container .esri-popup__content table tr:hover {
  background: #f8f8f8;
}
.esri-popup__button:hover {
  background: #002650 !important;
}
.esri-popup__button .esri-icon-close {
  color: #fff;
}
.esri-popup__button .esri-icon-close:hover {
  color: #68a0b9;
}
.esri-popup__button--dock {
  display: none !important;
}
.esri-popup__footer {
  margin: 0 0 3px 20px;
}
.esri-popup__footer .esri-popup__navigation {
  margin: 0;
  background: inherit;
  min-width: 80px !important;
  margin-right: 10px;
}
.esri-popup__footer .esri-popup__navigation .esri-popup__button {
  width: 20px !important;
}
.esri-popup__footer .esri-popup__navigation .esri-popup__feature-menu-button {
  width: 50px !important;
}
.esri-popup__footer .esri-popup__button {
  color: #000;
  font-size: 16px !important;
  width: 100% !important;
  max-width: 100% !important;
  display: flex !important;
  justify-content: flex-start !important;
  border: none;
  outline: none;
}
.esri-popup__footer .esri-popup__button:hover {
  background: inherit !important;
  color: #002650 !important;
}
.esri-popup__footer .esri-popup__button .esri-zoom-in-magnifying-glass {
  margin-right: 10px;
}

/* MOBILE */
@media only screen and (max-width: 1200px) {
  .esri-ui-top-left .esri-zoom {
    display: none;
  }
}

/* MEDIA QUERIES */
@media (max-width: 500px) {
  .esri-popup__main-container {
    width: 95% !important;
  }
  .esri-ui-top-left .esri-basemap-toggle {
    top: 700px;
  }
  .esri-ui-top-left .esri-zoom {
    top: 570px;
  }
}
